#menu {
    position: fixed;
    width: 300px;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.75);
    right: -300px;
    z-index: 10;
    a {
        z-index: 10;
        font-size: 2em;
        &:hover {
            text-shadow: 0 0 0.4em rgb(127, 202, 255);
        }
    }
    transition: transform .4s cubic-bezier(.77,0,.66,1.02);
    ul {
        text-align: right;
        width: 100%;
        padding: 0;
        top: 50%;
        position: relative;
        transform: translateY(-50%);
    }
    li {
        width: 100%;
        margin-top: 5px;
        margin-bottom: 5px;
        a {
            width: 100%;
        }
    }
    .menu-wrapper {
        max-width: 80%;
        margin-left: auto;
        margin-right: auto;
        height: 100%;
    }
}
#menu.open {
    transform: translateX(-300px);
}

.panel {
    width: calc(100vw - 100px);
    height: calc(100vh - 50px);
    position: fixed;
    top: 0;
    left: 0;
    margin: 25px 75px 25px 25px;
    text-align: center;
    .panel-inner {
        position: absolute;
        width: 50%;
        height: 100%;
        top: 0;
        left: 0;
        .panel-content {
            .title {
                h1 {
                    font-family: Manifesto;
                }
            }
        }
    }
    &#top .panel-inner {
        margin-left: 25%;
        margin-right: auto;
        display: table;
        .panel-content {
            display: table-cell;
            vertical-align: middle;
            padding: 150px 0 150px;
            width: 100%;
            text-align: center;
        }
        .title {
            h1 {
                font-size: 4.3em;
                margin: 0;
            }
            h3 {
                font-size: 1.6em;
                margin: 0;
                font-family: 'Montserrat Subrayada', sans-serif;
            }
        }
    }
}

.scrolldown{
	position:absolute;
	bottom:10px;
	right:50%;
	animation:mousemove 1.6s ease-in-out infinite;
}

.scrolldown span{
	position: absolute;
	left:-15px;
	bottom:45px;
	color: #eee;
	font-size: 0.7rem;
	letter-spacing: 0.05em;
}

.scrolldown span::after{
	content: "";
	position: absolute;
	top:10px;
	left:15px;
	width: 1px;
	height: 15px;
	background: #eee;
	animation: mousepathmove 1.4s linear infinite;
	opacity:0;
}

.scrolldown::before {
    content: "";
    position: absolute;
    bottom:0;
    left:-13px;
	width:25px;
	height:37px;
	border-radius: 10px;
	border:1px solid #eee;
}

.scrolldown:after{
	content:"";
	position: absolute;
	bottom:23px;
	left:-3px;
	width:5px;
	height: 10px;
	border-radius: 40%;
	border:1px solid #eee;
}

footer {
    position: fixed;
    bottom: 25px;
    right: 85px;
    small {
        margin-top: auto;
        margin-bottom: auto;
    }
    a {
        margin-left: 10px;
        height: 24px;
    }
}

@keyframes mousemove {
    0% {
        bottom: 50px;
    }
    50% {
        bottom: 40px;
    }
    100% {
        bottom: 50px;
    }
}

@keyframes mousepathmove {
    0% {
        height: 0;
        top: 10px;
        opacity: 0;
    }
    50% {
        height: 15px;
        opacity: 1;
    }
    100% {
        height: 0;
        top: 30px;
        opacity: 0;
    }
}